body {
  font-family: "Museo Sans Cyrl", sans-serif;
  line-height: 1.4;
  margin: 0;
  font-size: 14px;
  color: #333B4E;
  text-align: left;
  background-color: #ffffff;
}

.shadow {
  box-shadow: 0 4px 15px -8px #eaeaea;
}

.shadow-light {
  box-shadow: 0px 6px 16px rgba(169, 186, 205, 0.11);
}

.primary {
  color: #FE3362;
  border-color: #FE3362;
}

.gray {
  color: #B6C0CB;
  border-color: #B6C0CB;
}

.light-gray {
  color: #F4F7FB;
  border-color: #F4F7FB;
}

.dark-gray {
  color: #848E99;
  border-color: #848E99;
}

.white {
  color: #ffffff;
  border-color: #ffffff;
}

.black {
  color: #26323F;
  border-color: #26323F;
}

.white-bg {
  background: #ffffff;
}

.black-bg {
  background: #26323F;
}

.gray-bg {
  background: #B6C0CB;
}

.light-gray-bg {
  background: #F4F7FB;
}

.dark-gray-bg {
  background: #848E99;
}

.primary-bg {
  background: #FE3362;
}

.sky-bg {
  background: #F4F7FB;
}

a, button {
  transition: 300ms;
  background: none;
  border: none;
}

.icon-button {
  background: none;
  border: none;
  font-size: 16px;
  color: #B6C0CB;
}

.carousel-section {
  height: 100%;
}

.owl-carousel {
  position: relative;
  height: 100%;
}

.slide-count {
  left: 115px;
  bottom: 42px;
  background: rgba(0, 0, 0, 0.041);
  padding: 3px 5px;
}

@media only screen and (max-width: 767px) {
  .slide-count {
    bottom: 30px;
    left: 48%;
  }
}

.owl-theme .owl-nav {
  margin-top: 10px;
  position: absolute;
  right: 120px;
  bottom: 45px;
}

@media only screen and (max-width: 767px) {
  .owl-theme .owl-nav {
    margin-top: 10px;
    position: absolute;
    display: flex;
    justify-content: space-between;
    bottom: 25px;
    width: 100%;
    right: 0;
    padding: 0 15px;
  }
}

.owl-theme .owl-dots {
  position: absolute;
  bottom: 45px;
  left: 152px;
  display: flex;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .owl-theme .owl-dots {
    display: none;
  }
}

.owl-theme .owl-dots .owl-dot span {
  border-radius: 0;
}

.owl-carousel .owl-nav button.owl-next,
.owl-carousel .owl-nav button.owl-prev {
  border: none;
  border-radius: 0;
  font-size: 18px;
  height: 25px;
  width: 25px;
  color: #ffffff;
  background: #FE3362;
}

.owl-carousel .owl-nav button.owl-prev {
  background: #B6C0CB;
}

.owl-slide1,
.owl-slide2,
.owl-slide3 {
  height: 700px;
}

@media only screen and (max-width: 1366px) {
  .owl-slide1,
  .owl-slide2,
  .owl-slide3 {
    height: 600px;
  }
}

@media only screen and (max-width: 575px) {
  .owl-slide1,
  .owl-slide2,
  .owl-slide3 {
    height: 94vh;
  }
}

.cover {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.owl-slide1 {
  background-image: url("../img/slider/slide1.png");
}

@media only screen and (max-width: 1366px) {
  .owl-slide1 {
    background-position: right;
  }
}

@media only screen and (max-width: 767px) {
  .owl-slide1 {
    background-image: url("../img/slider/slide1-mobile.png");
  }
}

.owl-slide1__rectangle {
  right: 0;
  width: auto !important;
}

.owl-slide1__header {
  display: block;
  width: 90%;
}

.owl-slide2 {
  background-color: #F4F7FB;
  background-image: url("../img/slider/Mask Group.png");
}

@media only screen and (max-width: 767px) {
  .owl-slide2 {
    background-position: left;
  }
}

.owl-slide2__logo {
  width: 230px !important;
}

.owl-slide2__title {
  color: #26323F;
  width: 90%;
}

.owl-slide3 {
  background-image: url("../img/slider/slide3.png");
}

@media only screen and (max-width: 767px) {
  .owl-slide3 {
    background-position: left;
  }
}

.owl-slide3__description {
  width: 70%;
  margin-bottom: 5rem;
}

.owl-slide3__img {
  width: 80% !important;
}

.owl-slide3 button {
  color: #ffffff;
}

.owl-controls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
}

.owl-controls .owl-buttons div {
  position: absolute;
  top: 0;
  bottom: 0;
  display: inline-block;
  zoom: 1;
  margin: 0;
  width: 50px;
  height: 30px;
  line-height: 25px;
  text-align: center;
  font-size: .9em;
  border-radius: 3px;
  color: #ffffff;
  background: #000;
  opacity: .6;
  text-transform: capitalize;
}

.owl-controls .owl-buttons .owl-prev {
  left: 5px;
}

.owl-controls .owl-buttons .owl-next {
  right: 5px;
}

.action-section {
  background-color: #F4F7FB;
}

.bg-footer {
  background-color: #26323F;
}

.footer-bar__text {
  font-size: 12px;
  max-width: 230px;
}

.footer-bar__color-white {
  color: #ffffff;
}

.footer-bar__color-white-link {
  color: #ffffff;
  text-decoration: underline;
}

.footer-bar__color-red {
  color: #FE3362;
  text-decoration: underline;
}

.footer-bar__heading {
