.mobile-navbar__icon {
  font-size: 20px;
  margin-bottom: 5px;
}

.mobile-navbar span {
  font-size: 12px;
}

.mobile__search-box_hidden {
  visibility: hidden;
  opacity: 0;
}

.mobile__search-box_show {
  visibility: visible;
  opacity: 1;
}

.drawer-menu {
  background: #26323f;
  width: 87%;
  top: 0;
  flex: 1 0 auto;
  height: 100%;
  z-index: 1200;
  position: fixed;
  overflow-y: auto;
  flex-direction: column;
  -webkit-overflow-scrolling: touch;
  padding: 40px 20px 10px 20px;
}

.lp-section {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 100px 0;
}

@media only screen and (max-width: 1366px) {
  .lp-section {
    padding: 80px 0;
  }
}

@media only screen and (max-width: 991px) {
  .lp-section {
    padding: 50px 0;
  }
}

@media only screen and (max-width: 767px) {
  .lp-section {
    padding: 30px 0;
  }
}

.lp-section__header {
  margin-bottom: 80px;
}

@media only screen and (max-width: 1366px) {
  .lp-section__header {
    margin-bottom: 60px;
  }
}

@media only screen and (max-width: 991px) {
  .lp-section__header {
    margin-bottom: 40px;
  }
}

@media only screen and (max-width: 767px) {
  .lp-section__header {
    margin-bottom: 20px;
  }
}
