.details {
    &__font-header {
        @include respond-to(tablet) {
            font-size: 24px; }
        @include respond-to(phone-land) {
            font-size: 20px; } }
    &__img {
        @include respond-to(phone-land) {
            padding: 0; } }

    &__text-footer {
        bottom: 0;
        position: absolute;
        @include respond-to(tablet) {
            bottom: none;
            position: relative; }
        @include respond-to(phone-land) {
            bottom: none;
            position: relative; } } }
